}

.header__close-burger{
  display: block;
  margin-left: auto;
}

.header__navigation {
  padding: 15px;
  position: fixed;
  left: 0;
  bottom: 100%;
  background: var(--background);
  width: 100vw;
  height: 100vh;
  transition: .3s ease-in-out;
  overflow-y: auto;
  height: 100vh;
  z-index: 3;
  display: flex;
  flex-direction: column;
}

.header__navigation.opened{
  bottom: 0;
}

.header__navigation ul {
  flex-direction: column;
  margin: 0 auto;
  width: fit-content;
  width: -moz-fit-content;
  padding: 50px 15px;
  margin-top: auto;
  margin-bottom: auto;
  flex-shrink: 0;
}

.header__navigation li{
  display: grid;
  margin-bottom: 29px; 
  
  &.header_nav-title{
    margin-bottom: 20px;
  }
}

.header__navigation-item-container{
  color: var(--on-background);

  &:hover{
    a{
      color: var(--on-background-hover);
    }

    button svg path{
      stroke: var(--on-background-hover);
    }
  }
}

.has-child li{
  margin-bottom: 15px;
}

.has-child li a{
  font-size: 28px;
  line-height: 1.2;  
}

.header_nav-title{
  width: fit-content;
  display: block;
  margin: 0 auto;
}

.header__navigation a, .header_nav-title p{
  font-size: 34px;
  line-height: 41px;
  color: var(--on-background);
  padding: 5px;
  text-align: center;
}

