  padding: 0;
  padding-top: 15px;
  padding-bottom: 15px;
  margin-bottom: 12px;
  border-bottom: 5px solid var(--on-background);
}

.has-child.opened{
  margin-bottom: 0;

  &>.header__navigation-item-container{
    a{
      color: var(--on-background-hover);
    }
    button{
      transform: rotate(180deg) translateY(4px);
      stroke: var(--on-background-hover);;
    }
  }
}

.header__navigation-item-container{
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__navigation .has-child>ul {
  opacity: 1;
  overflow: hidden;
  visibility: visible;
  pointer-events: none;
  max-height: 0;
  padding-top: 0;
  padding-bottom: 0;
  position: static;
  /* transition: .3s max-height ease-in; */
  left: unset;
  top: unset;
}

.header__navigation .has-child.opened>ul{
  max-height: 999px;
  pointer-events: all;
  position: relative;
}

