.hero{
  padding-bottom: 192px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
  position: relative;

  .wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    min-height:calc(100vh - 270px);
    position: relative;
    text-align: center;
    word-break: break-all;

    &::after{
      position: absolute;
      content: "";
      height: 100%;
      width: 100%;
      max-width: 1325px;
      left: 50%;
      transform: translateX(-50%);
      top: 0;
      background: url("../img/hero.png") no-repeat;
      background-size: cover;
      
    }
  }

  h1,h2{
    position: relative;
    z-index: 2;
    padding: 0 25px;
  }
}

.hero__scroll-down{
  font-family: 'Montserrat';
  font-weight: 400;
  font-size: 22px;
  line-height: 1.2;
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  bottom: 137px;
  transform-origin:  left;
  padding: 0;
  color: var(--on-background);

  &::after{
    content: "";
    background: url(../img/arrow-down.svg) center no-repeat;
    background-size: contain;
    position: absolute;
    height: 75px;
    width: 20px;
    left: 50%;
    transform: translateX(-50%);
    top: calc(100% + 13px);
    animation: upNdown 2s ease-in-out;
    animation-iteration-count: infinite;
  }
}

@keyframes upNdown {
  0%{
    transform:  translateX(-50%) translateY(-5px);
  }
  50%{
    transform:  translateX(-50%) translateY(5px);
  }
  100%{
    transform:  translateX(-50%) translateY(-5px);
  }
}

