.page-separator{
  padding: 65px 0;
  position: relative;

  &::after{
    content: "";
    height: 2px;
    width: 100%;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    background: #C09891;
  }
}

.comments{
  margin-bottom: 75px;

  .wrapper{
    padding: 53px 126px 3px 126px;
    background: var(--surface);
    box-shadow: 4px 4px 25px rgba(0, 0, 0, 0.26);
  }
}

.comment{
  padding: 20px 44px;
  border: 2px solid #FFFFFF;
  margin-bottom: 50px;

  .comment-header{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    padding-bottom: 10px;
    border-bottom: 1px solid #FFFFFF;

    p{
      font-size: 18px;
      line-height: 27px;
      text-align: center;
      letter-spacing: 0.08em;
      color: #FFFFFF;
      padding: 0 0px;

      &:first-child{
        text-align: left;
      }

      &:last-child{
        text-align: right;
      }
    }

    h2{
      text-align: center;
      font-size: 20px;
      line-height: 1.5;
      text-align: center;
      letter-spacing: 0.08em;
      color: #FFFFFF;
      padding: 0 40px;
      margin: 0 auto;
      font-family: 'Montserrat';
    }
  }
}

.comment_text{
  font-size: 18px;
  line-height: 130%;
  text-align: center;
  letter-spacing: 0.08em;
  max-width: 875px;
  margin: 0 auto;
  margin-top: 35px;
  margin-bottom: 20px;
  color: #FFFFFF;
}

